<template>
  <v-container fluid>
    <div class="tables-basic">
      <h1 class="page-title mt-10 mb-6"> API  (Reqcontentsstatus)</h1>
      <v-row>
        <v-col cols="12" align="center">
          <v-card class="employee-list mb-1">
            <v-card-title class="pa-6 pb-3">
              <p>API </p>
              <v-spacer></v-spacer>
              <api-search></api-search>
            </v-card-title>
            <v-data-table 
              v-model="mock.Reqcontentsstatus.selected" 
              :headers="mock.Reqcontentsstatus.headers"
              :items="apiitem" 
              item-key="providerID"
              :page.sync="page"
              :items-per-page="itemsPerPage"
              hide-default-footer
              @page-count="pageCount = $event">
              <template v-slot:item="row">
                <tr>
                  <td></td>
                  <td>{{row.item.attributes.username}}</td>
                  <td>{{row.item.attributes.email}}</td>
                  <td>{{row.item.attributes.providerName}}</td>
                  <td>{{row.item.attributes.createdAt}}</td>
                  <td>{{row.item.attributes.endYMD}}</td>
                  <td>{{row.item.attributes.endOrNot}}</td>
                  <td>
                  <v-btn color="primary" depressed small @click="submit(row.item)">
                    {{ label }}
                  </v-btn>
                    <!-- <v-switch
                      v-model="row.item.attributes.Usestatuses"
                      :label="`: ${Usestatuses?'' : ''}`"
                    ></v-switch> -->
                  </td>
                </tr>
              </template>
            </v-data-table>
            <div class="text-center pt-2">
            <v-pagination
              v-model="page"
              :length="6"
            ></v-pagination>  
            </div>
            <!-- <v-btn depressed color="primary" large>   </v-btn> -->
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
// import { postApiUsingRequestStatuses } from '../../api/index.js'
import { getApiUsingRequestStatuses } from '../../api/index.js'

import mock from './mock'

export default {
  components: {
  },
  name: 'Reqcontentsstatus',
  data() {
    return {
      apiitem:[],
      mock,
      //
      // Usestatuses: true,
      label: ''
    }
  },

  created(){
    var vm = this;
    getApiUsingRequestStatuses()
    .then(function(res){
      vm.apiitem = res.data.data;
      console.log(res.data.data);
    })
    .catch(function(error){
      console.log(error);
    })
  },

  method: {
    submit() {
      this.label = ''
    }
  }
}

</script>

<style src="./Basic.scss" lang="scss"></style>
